body{
	font-family: Montserrat , sans-serif;
	color: white;
	background-size: contain ;
	background-position: center center;
	background-image: url('./img/bg.jpg');

}
html{
	background: #000000;
}
*{
	margin: 0;
	padding: 0;
	-webkit-transition: 0.2s;
	-o-transition: 0.2s;
	transition: 0.2s;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
#root {
	height: 100vh;
	width: 100%;
	
}
.modal, .lost, .win , .tie{
	opacity: 0.97;
	position: fixed;
	left:50%;
	right: 50%;
	top:50%;
	z-index: 500;
	-webkit-transform: translate(-50%, -50%);
	    -ms-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	border: 1px solid;
	width:500px;
	height: 200px;
	border-radius: 35px;	
	display: -ms-flexbox;	
	display: flex;
	-ms-align-items: center;
	-ms-flex-align: center;
	    align-items: center;
	-webkit-transition: ease 0.6s;
	-o-transition: ease 0.6s;
	transition: ease 0.6s;
	-ms-flex-direction: column;
	    flex-direction: column;
}
.modal{background: #caff51;}
.lost{background: #ff3232;}
.win{background: #1bf734;}
.tie{background: #f8fc00;}
.modal span, .lost span, .win span, .tie span {
	font-size: 30px;
	font-weight: bold;
	margin-top: 35px;
	text-align: center;
	margin-left: auto;
	display: block;
	margin-right: auto;
}
.modal__button, .lost button, .win button, .tie button{
	outline:none;
	border: 3px solid white;
	width: 300px;
	height: 50px;
	border-radius: 35px;
	font-size: 40px;
	font-weight: bold;
	background: #ff3232;
	color: white;
	letter-spacing: 1px;
	margin-top: 60px;
	-webkit-transition: ease 0.2s;
	-o-transition: ease 0.2s;
	transition: ease 0.2s;
	text-align: center;
	display: block;
	margin-left: auto;
	margin-right: auto;
}
.modal__button:hover, .lost button:hover, .win button:hover, .tie button:hover{
	background: #1bf734;
	letter-spacing: 3px;
	
}
.enemy{
	-webkit-background-size: cover;
	border: 2px solid white;
	border-radius: 5px;
	width: 100px;
	height: 100px;
	background-size: cover;
	background-position: center;
	background-image: url('http://justclickit.ru/turtles/img/bibop2.jpg');
	margin-bottom: 15px;
}
.im{
	-webkit-background-size: cover;
	border: 2px solid white;
	border-radius: 5px;
	width: 100px;
	height: 100px;
	background-size: cover;
	background-position: center;
	background-image: url(img/sema.jpg);
	margin-bottom: 15px;
}
.imWrap{
	position: fixed;
	bottom: 65px;
	right: 50%;
	left: 50%;
	-webkit-transform: translate(-50%);
	    -ms-transform: translate(-50%);
	        transform: translate(-50%);
	width: 100px;
	height: 100px;
	text-align: center;
}
.enemyWrap{
	position: fixed;
	top: 30px;
	right: 50%;
	left: 50%;
	-webkit-transform: translate(-50%);
	    -ms-transform: translate(-50%);
	        transform: translate(-50%);
	width: 100px;
	height: 100px;
	text-align: center;
}
.imWrap span , .enemyWrap span {
	font-size: 20px;
	font-weight: bold;
	color: white;
} 
.Hit{
	position: fixed;
	right: 50px;
	bottom: 35px;
}
.Stand{
	position: fixed;
	right: 200px;
	bottom: 35px;
}
.Hit , .Stand {
	height: 50px;
	width: 100px;
	background: white;
	border-radius: 25px;
	font-size: 25px;
	border: none;
	outline: none;
	-webkit-transition: .2s ease;
	-o-transition: .2s ease;
	transition: .2s ease;
}
.Hit:hover , .Stand:hover {
	background: transparent;
	border: 1px solid white;
	letter-spacing: 1px;
	color: white;
}
.cards1{
	width: 100%;
	height: 150px;
	position: fixed;
	top:35%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	margin: 0;
	padding: 0;
}
.cards2{
	width: 100%;
	height: 150px;
	position: fixed;
	top:55%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	margin: 0;
	padding: 0;
}
.cards1 img, .cards2 img {	
	width: 150px;
	height: 150px;
	margin-right: 20px;
	margin-left: 20px;
 }
 @media screen and (min-width: 800px) {
	.cards1 img, .cards2 img {
		width: 200px;
		height: 200px;
		
	}
	.cards1  {
		top: 30%;
	}
} 
@media screen and (min-width: 1200px){
	.cards1 img, .cards2 img {
		margin-right: 14px;
		margin-left: 14px;
	}
}
@media screen and (max-height: 800px) {
	.cards1 img, .cards2 img {
		width: 160px;
		height: 160px;
	}
	.cards1  {
		top: 28%;
	}
}
@media screen and (max-width: 800px) {
	.cards1 img, .cards2 img {
		width: 160px;
		height: 160px;
	}
	.cards1  {
		top: 30%;
	}
	.Stand {
		right: 178px;	
	}
	.Hit {
		right: 36px;
	}
}
@media screen and (max-width: 750px) {
	.Hit {
		right: 31px;
	}
	.Stand {
		right: 159px;
	}
}  
@media screen and (max-width: 750px) {
	.Hit {
		right: 25px;
	}
	.Stand {
		right: 140px;
	}
}
@media screen and (max-height: 720px) {
	.cards1 img, .cards2 img {
		width: 140px;
		height: 140px;
	}
} 
 @media screen and (max-height: 670px) {
	.cards1 img, .cards2 img {
		width: 110px;
		height: 110px;
	}
	.cards1 {
		top: 32%;
	}
	.enemy, .im {
		height: 70px;
		width: 70px;
	}
	.imWrap, .enemyWrap {
		width: 70px;
	}
	.Stand , .Hit {
		width: 70px;
		height: 35px;
		font-size: 16px;
	}
	.cards1 img, .cards2 img {
		margin-left: 0px;
		margin-right: 0;
	}
}
@media screen and (max-width: 600px) {
	.Stand , .Hit {
		width: 70px;
		height: 35px;
		font-size: 16px;
	}
	.cards1 img, .cards2 img {
		margin-left: 0px;
	}
}
@media screen and (max-height: 600px) {
	.cards1 img, .cards2 img {
		width: 90px;
		height: 90px;
	}
	.cards1 img, .cards2 img {
		margin-left:10px;
		margin-right: 10px;
	}
	.cards2 {
		top: 52%;
	}
	.imWrap {
		bottom: 30px;
	}
	.enemyWrap {
		top: 8px;
	}
}
@media screen and (max-width: 500px) {
	.cards1 img, .cards2 img {
		margin-left: -10px;
		margin-right: -10px;
	}
	
	.Stand {
		right: 100px;
		height: 25px;
		width: 50px;
		font-size: 12px;
	}
	.Hit {
		right: 20px;
		height: 25px;
		width: 50px;
		font-size: 12px;
	}
}
@media screen and (max-width: 430px) {
	.Stand {
		right: 70px;
	}
	.Hit {
		right: 15px;
	}
}


